.upload-file-site {
  line-height: 1.5;
}
.upload-file-site .dragger-plus {
  margin-bottom: 8px;
}
.upload-file-site .rectangle-plus {
  margin-bottom: 8px;
}
.upload-file-site .file-list {
  overflow: hidden;
}
.upload-file-site .file-item {
  padding: 6px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  overflow: hidden;
}
.upload-file-site .file-item.err {
  border: 1px solid red;
}
.upload-file-site .file-item .file-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  overflow: hidden;
}
.upload-file-site .file-item .file-icon img {
  max-height: 100%;
  max-width: 100%;
}
.upload-file-site .file-item .upload-rate {
  width: 100%;
  height: 4px;
  background-color: #d9d9d9;
  border-radius: 2px;
  overflow: hidden;
}
.upload-file-site .file-item .upload-rate .rate {
  height: 100%;
  background-color: springgreen;
  width: 0%;
  -webkit-transition: width 300ms;
  transition: width 300ms;
}
.upload-file-site .file-item .upload-err {
  color: red;
}
.upload-file-site .file-item .file-btns {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.upload-file-site .file-item .file-btns .file-btn {
  margin-right: 6px;
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}
.upload-file-site .file-item .file-btns .file-btn:last-of-type {
  margin-right: 0;
}
.upload-file-site .file-item .file-btns .file-btn.btn-cancel {
  color: red;
  font-size: 12px;
  cursor: pointer;
}
.upload-file-site .file-item .file-btns .file-btn:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
.upload-file-site .file-rectangle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  height: 62px;
  margin-top: 8px;
}
.upload-file-site .file-rectangle:first-of-type {
  margin-top: 0;
}
.upload-file-site .file-rectangle .file-icon {
  width: 48px;
  height: 48px;
}
.upload-file-site .file-rectangle .file-meddle {
  flex: 1 1;
  font-size: 12px;
  padding: 0 10px;
}
.upload-file-site .file-rectangle .file-meddle .file-name {
  word-break: break-all;
}
.upload-file-site .file-rectangle .file-meddle .upload-rate {
  margin-top: 4px;
}
.upload-file-site .file-rectangle .file-meddle .upload-err {
  margin-top: 2px;
}
.upload-file-site .file-rectangle .file-meddle .upload-success {
  margin-top: 2px;
  color: springgreen;
}
.upload-file-site .file-square {
  position: relative;
  float: left;
  height: 72px;
  width: 72px;
  margin-right: 4px;
  margin-bottom: 4px;
}
.upload-file-site .file-square:last-of-type {
  margin-right: 0;
}
.upload-file-site .file-square.square-plus {
  border: 1px dashed #d9d9d9;
  -webkit-transition: border-color 500ms;
  transition: border-color 500ms;
}
.upload-file-site .file-square.square-plus:hover {
  border-color: #6eafec;
}
.upload-file-site .file-square:hover .file-btns {
  opacity: 1;
}
.upload-file-site .file-square .file-icon {
  height: 100%;
  width: 100%;
}
.upload-file-site .file-square .status-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  padding: 6px;
  width: 100%;
  height: 100%;
}
.upload-file-site .file-square .status-wrapper .upload-rate {
  height: 8px;
}
.upload-file-site .file-square .status-wrapper .upload-err {
  font-size: 10px;
  line-height: 14px;
  padding: 1px 4px;
  min-width: 100%;
  margin: 0 -6px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.upload-file-site .file-square .file-btns {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  padding: 8px;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.upload-modal-body {
  width: 100%;
  overflow-x: auto;
}
.upload-modal-body .upload-image-crop {
  display: flex;
  height: 420px;
  width: 700px;
  padding-bottom: 20px;
  overflow: auto;
}
